.wrap {
    margin: auto;
    padding: 24px 32px 22px;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 432px;
    box-sizing: border-box;
}

.title {
    margin: 0 0 24px 0;
    font-size: 16px;
    line-height: 100%;
    color: #000;
}

.inner {
    position: relative;
    height: 100%;
}

.preloader {
    margin: auto;
    height: 124px;
    width: 124px;
    background: url(preloader.gif);
    background-size: contain;
}

.error_container {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
   	align-items: center;
	justify-content: center;
}

.error_img {
    margin: 0 0 24px 0;
    max-width: 267px;
    max-height: 164px;
}

.error_subtitle {
    margin: 0 0 8px 0;
    font-size: 18px;
    line-height: 21px;
}

.error_text {
    margin: 0; 
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #999;
}

.error_link {
    color: #356EFF;
}

.list {
    margin: 0 0 12px;
    padding: 0;
}

.list__item {
    padding: 6px 0 6px 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 52px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    border-radius: 10px;
    list-style-type: none;
}

.list__item:hover {
    border: 1px solid #356EFF;
}

.list__item:not(:last-child) {
    margin: 0 0 8px 0;
}

.list__item p {
    margin: 0;
}

.name {
    display: inline-block;
    /*font-family: "Roboto", sans-serif;*/
    font-size: 14px;
    line-height: 16px;
    color: #000;
    text-decoration: inherit;
}

.name:hover {
    opacity: .8;
    transition: .3s;
}

.info {
    display: flex;
    align-items: center;
}

.circle {
    margin: 0 5px 0 0;
	min-width: 16px;
	min-height: 16px;
    border-radius: 50%;
    background-color: #696969
}

.html {
	background-color: #e00000;;
}

.css {
	background-color: #0094ff;	
}

.js {
	background-color: #ffc700;	
}

.language {
    margin: 0 16px 0 0;
    width: 80px;
    /*font-family: "Roboto", sans-serif;*/
    font-size: 14px;
    line-height: 16px;
    color: #999;
}

.star,
.forks {
    margin: 0 15px 0 0;
	display: flex;
    align-items: center;
    justify-content: space-between;
    /*font-family: "Roboto", sans-serif;*/
    font-size: 14px;
    line-height: 16px;
    color: #999;
}

.star img,
.forks img {
    margin: 0 5px 0 0;
}

.update {
    /*font-family: "Roboto", sans-serif;*/
    font-size: 14px;
    line-height: 16px;
    color: #999;
}

.pagination {
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%);
    display: flex;
    justify-content: center;
}

.button {
    padding: 8px 16px;
    width: 73px;
    height: 32px;
    box-sizing: border-box;
    border: 1px solid #999;
    border-radius: 37px;
    background: #fff;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    line-height: 16px;
    color: #356eff;
    outline: none;
    cursor: pointer
}

.button:first-of-type {
    margin: 0 8px 0 0;
}

.button:hover {
    background: #356EFF;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.25);
    color: #fff;
}

.disabled {
    opacity: .6;
}

@media (max-width: 430px) {

    .wrap {
        padding: 24px 14px 22px;
    }

    .title {
        margin: 0 0 24px 0;
    }
        
    .language,
    .update {
        font-size: 12px;
        line-height: 14px;
    }
}

@media (max-width: 360px) {

    .wrap {
        padding: 24px 8px 22px;
    }
}